import {useEffect, useState} from "react";

function AgeAsyncTest(){
        let [count, setCount] = useState(0);
        let [age, setAge] = useState(20);
        useEffect(()=>{
            //page  useEffect .
            //  count!==0    .
            if(count!==0 && count<3)
                setAge(age + 1);
            else
                console.log('stop')
        },[count])
    /*
        function ageUpTest(){
            setCount(count+1);
            if(count<3){
                setAge(age+1);
            }else{
                console.log('stop');
            }
        }*/
        return (
            <div>
                <div>  {age}  3   .  : {count}</div>
                <button onClick={()=>{setCount(count+1)}}></button>
            </div>
        )
    }

export default AgeAsyncTest;